import React from 'react';

import Layout from "../components/layout";
import FileScanner from '../components/examples/scanner/FileScanner';
import CameraScanner from '../components/examples/scanner/CameraScanner';
import InputScanner from '../components/examples/scanner/InputScanner';
import Alert from '../components/Alert';

import BrIcon from '../styles/img/svg/brand-manual.svg';
import PlusIconSm from '../styles/img/svg/br-add-sm.svg';
import Loading from '../styles/img/svg/loading-40.svg';
import hardCardImg from '../images/hard-card-sm.png';
import SEO from "../components/seo"

export default class Scanner extends React.Component{
  constructor(props){
    super(props);

    this.state = {
      barcode: '',
      isLoading: false,
      step: 1,
      cardsAmount: 1,
      activated: false,
      alert: {
        show: false
      }
    }
  }

  handleUpdateState = (s, isLoading) => {
    s.isLoading = isLoading;
    this.setState(s, isLoading ? this.fakeRequest : null);
  }

  fakeRequest = () => {
    setTimeout(() => {
      this.setState({isLoading: false});
    }, 2000)
  }

  handleActivate = () => {
    this.handleUpdateState({step: 3, activated: true} , true);
  }

  handleAmountChange = (e, d) => {
    if(!e.target){
      this.setState(prevState => {
        let amount = prevState.cardsAmount;

        return{
          cardsAmount: e === 'plus' ? amount + 1 : (amount === 1 ? amount : amount - 1)
        }
      })
    } else {
      this.setState({cardsAmount: e.target.value})
    }
  }

  handleStartOver = () => {
    this.setState({
      barcode: '',
      isLoading: false,
      step: 1,
      cardsAmount: 1,
      activated: false,
    })
  }

  showMessage = (message, type) => {
    this.setState({
      alert: {
        message: message,
        type: type,
        show: true
      }
    })
  }

  onAlertClose = () => {
    this.setState({alert: {
        show: false
      }});
  }

  render(){
    return(
      <Layout>
        <SEO title="Hard Cards" />
        <div className="container">
          <Alert
            type={this.state.alert.type}
            message={this.state.alert.message}
            show={this.state.alert.show}
            onAlertClose={this.onAlertClose}
          />
          <div className="island px-4 py-3">
            <h2>
              <BrIcon className="mr-2" />
              Activate Hard Card
            </h2>
            <ol className="breadcrumb mx-n4 px-4 mt-3">
              <li className={"breadcrumb-item " + (this.state.step === 1 ? 'active' : '')}>Scan Card</li>
              <li className={"breadcrumb-item " + (this.state.step === 2 ? 'active' : '')}>Check Card Info</li>
              <li className={"breadcrumb-item " + (this.state.step === 3 ? 'active' : '')}>Activate Card</li>
            </ol>
            <div className={"scanner " + (this.state.isLoading ? 'loading' : '')}>
              <Loading className="loading-img" />
              <div className={"row mt-5 mb-4 " + (this.state.step === 1 ? '' : 'd-none')}>
                <div className="col-lg-6">
                  <CameraScanner handleUpdateState={this.handleUpdateState} showMessage={this.showMessage} />
                  <div className="devider d-none d-xl-block" />
                </div>
                <div className="col-lg-6">
                  <div className="manual-area mx-auto mt-4 mt-md-0">
                    <hr className="my-4 d-none d-md-block d-lg-none" />
                    <h6 className="text-center"><strong>You can also:</strong></h6>
                    <FileScanner handleUpdateState={this.handleUpdateState} showMessage={this.showMessage} />
                    <hr className="my-4 d-none d-md-block" />
                    <InputScanner handleUpdateState={this.handleUpdateState} value={this.state.barcode} />
                  </div>
                </div>
              </div>
              <div className={"row mt-5 mb-4 " + (this.state.step !== 1 ? '' : 'd-none')}>
                <div className="col">
                  <div className="card-area d-flex flex-column flex-md-row">
                    <div className="card-area-img px-4 py-3 rounded-lg text-center">
                      <img src={hardCardImg} alt="Hard" />
                    </div>
                    <div className="card-area-dscr ml-md-5 mt-3 mt-n-md-0">
                      <h6 className="font-weight-bold">Pinless $10</h6>
                      <div className="mt-1">Card Number: {this.state.barcode}</div>
                      <div className="mt-1">Status:
                        <span className={"text-uppercase font-weight-bold " + (this.state.activated ? 'text-success' : '')}>
                                                  {this.state.activated ? ' Activated' : ' Not Activated'}
                                              </span>
                      </div>
                      <div className="mt-1">Short card description if any.</div>
                      <div className="mt-1">50 pieces batch</div>
                      <div className={"mt-1 " + (this.state.step === 2 ? 'd-none' : '')}> Amount: {this.state.cardsAmount} piece(s)</div>
                      <div className={"card-amount mt-2 justify-content-center justify-content-md-start " + (this.state.step === 3 || this.state.activated ? 'd-none' : 'd-flex')}>
                        <button className="btn btn-outline-primary btn-sm btn-icons" onClick={this.handleAmountChange.bind(this, 'minus')} disabled={this.state.cardsAmount === 1}>-</button>
                        <input type="text" className="form-control form-control-sm border-0 shadow-none text-center font-weight-bold" value={this.state.cardsAmount} onChange={this.handleAmountChange.bind(this)} />
                        <button className="btn btn-outline-primary btn-sm btn-icons" onClick={this.handleAmountChange.bind(this, 'plus')}>
                          <PlusIconSm />
                        </button>
                      </div>
                      <div className="activate mt-3 d-flex">
                        <button className={"btn btn-primary flex-grow-1 flex-md-grow-0 " + (this.state.step === 2 && !this.state.activated ? '' : 'd-none')} onClick={this.handleActivate.bind(this)}>Activate Now</button>
                        <button className={"btn btn-outline-secondary " + (this.state.step === 3 || this.state.activated ? '' : 'd-none')} onClick={this.handleStartOver}>
                          Scan another card
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    )
  }
}